/* Tabs styling to match Work Orders pill tabs */
.tabs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  gap: 1rem;
  margin-bottom: 1rem;
}
.tabs .tab {
  border-radius: 12px;
  padding: 0.75rem 0;
  font-weight: 500;
  color: #64748b;
  cursor: pointer;
  transition: all 0.2s ease;
  background: #f3f4f6;
}
.tabs .tab:hover {
  background: #e5e7eb;
}
.tabs .tab.active {
  background: #2563eb;
  color: #fff;
  font-weight: 600;
}

/* Inventory-style navigation tabs fallback (if used) */
.navigation-tabs .tabs-nav {
  display: flex;
  gap: 12px;
}
.navigation-tabs .tab-link {
  display: inline-block;
  padding: 12px 16px;
  border-radius: 12px;
  background: #f3f4f6;
  color: #64748b;
  font-weight: 500;
  cursor: pointer;
  text-decoration: none;
}
.navigation-tabs .tab-link.active {
  background: #2563eb;
  color: #fff;
}

.asset-list-page .asset-card-checkbox {
  width: 20px !important;
  height: 20px !important;
  border-radius: 50% !important;
  border-width: 2px;
  border-color: #3b82f6; /* blue-500 */
  appearance: none;
  background-color: #fff;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  transition: border-color 0.2s;
}
.asset-list-page .asset-card-checkbox:checked {
  background-color: #3b82f6;
  border-color: #2563eb;
}
.asset-list-page .asset-card-checkbox:checked::after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  top: 3px;
  left: 3px;
}
.asset-list-page .asset-card-header {
  background: #e0edff;
  height: 8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  overflow: hidden;
}
.asset-list-page .asset-card {
  border-radius: 1rem;
  box-shadow: 0 2px 8px 0 rgba(16, 30, 54, 0.08);
  overflow: hidden;
  background: #fff;
  border: 1px solid #bfdbfe;
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.2s;
}
.asset-list-page .asset-card hr {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  border-color: #e5e7eb;
}
